body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#grid {
  display: grid;
  grid-template-columns: 100vw;
  grid-template-rows: 100vh;
  width: 200vw;
  height: 200vh;
}

.item-1-1 {
  grid-row: 1;
  grid-column: 1;
  background: red;
}

.item-1-2 {
  grid-row: 1;
  grid-column: 2;
  background: green;
}

.item-2-1 {
  grid-row: 2;
  grid-column: 1;
  background: blue;
}

.item-2-2 {
  grid-row: 2;
  grid-column: 2;
  background: purple;
}



