/* ----- Variables ----- */
:root {
  --transition-duration: .4s;
  --transition-timing  : cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

/**
 * ================================================================
 *                               CUBE
 * ================================================================
 */
.Cube[react-cube-nav] {
  position: relative;
  width   : 100vw;
  height  : 100vh;
  overflow: hidden;
}

/**
 * ================================================================
 *                               LAYER
 * ================================================================
 */
/* ----- General ----- */
.Layer[react-cube-nav] {
  position             : absolute;
  display              : grid;
  grid-template-columns: 100vw;
  grid-template-rows   : 100vh;

  --Layer-tx: calc((var(--Layer-col, 1) - 1) * -100vw);
  --Layer-ty: calc((var(--Layer-row, 1) - 1) * -100vh);
  --Layer-ox: calc((var(--Layer-col, 1) - 1) * 100vw + 50vw + 3%);
  --Layer-oy: calc((var(--Layer-row, 1) - 1) * 100vh + 50vh - 3%);

  -webkit-transform-origin:
    var(--Layer-ox)
    var(--Layer-oy);

      -ms-transform-origin:
    var(--Layer-ox)
    var(--Layer-oy);

          transform-origin:
    var(--Layer-ox)
    var(--Layer-oy);

  -webkit-transition:
    opacity   var(--transition-duration) var(--transition-timing)
  , -webkit-transform var(--transition-duration) var(--transition-timing);

  transition:
    opacity   var(--transition-duration) var(--transition-timing)
  , -webkit-transform var(--transition-duration) var(--transition-timing);

  -o-transition:
    opacity   var(--transition-duration) var(--transition-timing)
  , transform var(--transition-duration) var(--transition-timing);

  transition:
    opacity   var(--transition-duration) var(--transition-timing)
  , transform var(--transition-duration) var(--transition-timing);

  transition:
    opacity   var(--transition-duration) var(--transition-timing)
  , transform var(--transition-duration) var(--transition-timing)
  , -webkit-transform var(--transition-duration) var(--transition-timing);
}

/* ----- Depth layering ----- */
.Layer[react-cube-nav].current {
  opacity  : 1;
  -webkit-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(1);
      -ms-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(1);
          transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(1);
}

.Layer[react-cube-nav].in,
.Layer[react-cube-nav].out {
  opacity: 0;
  pointer-events: none;
}

.Layer[react-cube-nav].in {
  -webkit-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(.5);
      -ms-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(.5);
          transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(.5);
}

.Layer[react-cube-nav].out {
  -webkit-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(2);
      -ms-transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(2);
          transform:
    translate(var(--Layer-tx), var(--Layer-ty))
    scale(2);
}


/**
 * ================================================================
 *                               FACE
 * ================================================================
 */
.Face[react-cube-nav] {
  grid-row   : var(--Face-row, 1);
  grid-column: var(--Face-col, 1);
  width      : 100vw;
  height     : 100vh;

  opacity       : 0;
  pointer-events: none;

  -webkit-transition: opacity var(--transition-duration) var(--transition-timing);

  -o-transition: opacity var(--transition-duration) var(--transition-timing);

  transition: opacity var(--transition-duration) var(--transition-timing);
}

.Face[react-cube-nav].current {
  opacity       : 1;
  pointer-events: initial;
}



